<template>
  <div>
    <h1 class="fw-bold title-page">Danh sách hợp đồng đã thanh toán</h1>
    <div class="row">
      <div class="col-12">
        <div class="d-flex justify-content-between mb-3">
          <div>
            <div class="input-group">
              <div class="dropdown">
                <button
                  class="btn btn-outline-secondary"
                  type="button"
                  data-bs-toggle="dropdown"
                  aria-expanded="false"
                  data-bs-auto-close="outside"
                >
                  <i class="fa-regular fa-gear"></i>
                </button>
                <ul class="dropdown-menu">
                  <li>
                    <div class="dropdown-item">
                      <div class="form-check">
                        <input
                          class="form-check-input"
                          type="checkbox"
                          :value="searchBy.company_name"
                          v-model="searchBy.company_name"
                          id="flexCheckDefault"
                        />
                        <label class="form-check-label" for="flexCheckDefault">
                          Tìm theo tên công ty
                        </label>
                      </div>
                    </div>
                  </li>
                  <li>
                    <div class="dropdown-item">
                      <div class="form-check">
                        <input
                          class="form-check-input"
                          type="checkbox"
                          :value="searchBy.taxcode"
                          v-model="searchBy.taxcode"
                          id="flexCheckDefault"
                        />
                        <label class="form-check-label" for="flexCheckDefault">
                          Tìm theo mã số thuế công ty
                        </label>
                      </div>
                    </div>
                  </li>
                  <li></li>
                </ul>
              </div>
              <div class="position-relative d-inline-flex align-items-center">
                <input
                  type="text"
                  class="form-control"
                  v-model="keyword"
                  :placeholder="searchBy.placeholder"
                  aria-label="Username"
                  aria-describedby="basic-addon1"
                  @change="search()"
                />
              </div>
              <div class="dropdown ms-1">
                <button
                  class="btn btn-primary-soft"
                  type="button"
                  data-bs-toggle="dropdown"
                  aria-expanded="false"
                  data-bs-auto-close="outside"
                >
                  <i class="fa-solid fa-bars-filter"></i> Lọc
                </button>
                <ul class="dropdown-menu p-3" style="width: 500px">
                  <li><h5 class="fw-bold">Tìm kiếm</h5></li>
                  <li>
                    <div>
                      <div class="row">
                        <div class="col-6">
                          <div class="form-group">
                            <label for="">Từ ngày</label>
                            <date-picker
                              v-model.lazy="searchBy.startDate"
                              :config="options"
                            ></date-picker>
                          </div>
                        </div>

                        <div class="col-6">
                          <div class="form-group">
                            <label for="">Đến ngày</label>
                            <date-picker
                              v-model.lazy="searchBy.endDate"
                              :config="options"
                            ></date-picker>
                          </div>
                        </div>
                      </div>
                    </div>
                  </li>
                  <li>
                    <hr class="dropdown-divider" />
                  </li>
                  <li>
                    <div class="row">
                      <div class="col-6">
                        <div class="form-group">
                          <label for="">Trạng thái hợp đồng</label>
                          <select class="form-select" v-model="searchBy.contract_status">
                            <option value="-1">Tất cả</option>
                            <option value="0">Mới tạo lập</option>
                            <option value="1">Đã trình ký</option>
                            <option value="2">Đã gửi khách hàng</option>
                            <option value="3">Hoàn thiện</option>
                            <option value="4">Đã làm thanh toán</option>
                          </select>
                        </div>
                      </div>

                      <div class="col-6">
                        <div class="form-group">
                          <label for="">Trạng thái thanh toán</label>
                          <select v-model="searchBy.payment_status" class="form-select">
                            <option value="-1">Tất cả</option>
                            <option value="0">Chưa thanh toán</option>
                            <option value="1">Đã nhận tiền nhưng chưa nộp tiền</option>
                            <option value="2">
                              Đã nộp tiền nhưng chưa làm thanh toán
                            </option>
                            <option value="3">Đã nộp tiền và xuất hóa đơn</option>
                          </select>
                        </div>
                      </div>
                    </div>
                  </li>
                  <li>
                    <button class="btn btn-primary-soft mt-2" @click="advanceSearch">
                      <i class="fa-solid fa-bars-filter"></i> Lọc
                    </button>

                    <button class="btn btn-secondary mt-2 ms-1" @click="search">
                      <i class="fa-regular fa-rotate"></i> Reset
                    </button>
                  </li>
                </ul>
              </div>
            </div>
          </div>

       
        </div>
      </div>
    </div>
    <table class="table table-responsive">
      <thead>
        <tr>
          <th width="10">#</th>
          <th width="45%">Tên công ty</th>
          <th width="10%">Mã số thuế</th>
          <th>Ngày thanh toán</th>
          <th>Trạng thái</th>
          <th>Tổng tiền (VAT)</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in contracts" :key="item.contract_id">
          <td>{{ index + 1 }}</td>
          <td>
            <router-link :to="{
              name:'contract-detail',
              params:{
                id : item.contract_id
              }
            }">
              <span class="text-primary">{{ item.company_name }}</span>
            </router-link>
          </td>
          <td>{{ item.company_taxcode }}</td>
          <td>{{ item.contract_payment_date | moment("DD/MM/YYYY HH:mm:ss") }}</td>
          <td>
            <span class="badge text-bg-success text-wrap">{{
              paymentStatus(item.contract_payment)
            }}</span>
          </td>
          <td>
            <strong class="text-danger">{{ item.contract_total | formatamout }}đ</strong>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import axios from "axios";
import moment from "moment";
import datePicker from "vue-bootstrap-datetimepicker";
import "pc-bootstrap4-datetimepicker/build/css/bootstrap-datetimepicker.css";
export default {
  components:{
    datePicker,
  },
  data() {
    return {
      keyword: "",
      contracts: [],
      searchBy: {
        taxcode: false,
        company_name: false,
        placeholder: "Tìm theo tên công ty",
        startDate: moment().startOf("month").format("DD/MM/YYYY"),
        endDate: moment().add(2, "months").startOf("month").format("DD/MM/YYYY"),
        payment_status: -1,
        contract_status: -1,
        page: 1,
      },
      options: {
        format: "DD/MM/YYYY",
        useCurrent: false,
      },
    };
  },
  created() {
    this.getContracts();
  },
  methods: {
    advanceSearch() {
      this.getContracts({
        date: {
          startDate: this.searchBy.startDate,
          endDate: this.searchBy.endDate,
        },
        page: this.pagination.current_page,
        payment: this.searchBy.payment_status,
        status: this.searchBy.contract_status,
      });
    },
    search() {
      this.getContracts({
        page: this.pagination.current_page,
        name: this.keyword,
      });
    },
    async getContracts(searchBy) {
      await axios.post("contractpayment/fetchAll",searchBy).then((response) => {
        this.contracts = response.data;
      });
    },
    paymentStatus(status) {
      return this.$econtract.paymentStatus(status);
    },
  },
};
</script>
